<template>
  <!-- Ìó§Îçî -->
  <div class="header">
    <ul class="header-button-left">
      <li v-if="step>0" @click="step--">Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step==1" @click="step++">Next</li>
      <li v-if="step==2" @click="publish">Write</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <!-- Ïª®ÌÖåÏù¥ÎÑà -->
  <ContainerBox @write="myWriting = $event" 
                @filterChanged="filter = $event" 
                @likeChanged="likeFunc($event)"
                @markChecked="markFunc($event)"
                @postDeleted="delFunc($event)"
                :postData="postData" :step="step" :image="image" />
  <infinite-loading v-if="step==0" @infinite="infiniteHandler">
    <template #complete>
      <span class="more">No more data found!</span>
    </template>
  </infinite-loading>

  <!-- Ìë∏ÌÑ∞ -->
  <div class="footer" v-if="step === 0">
    <ul class="footer-button-plus">
      <input type="file" id="file" class="inputfile" @change="upload" />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>
</template>

<script>
import ContainerBox from "./components/ContainerBox.vue";
import Data from "./post.js";
import axios from "axios";
import InfiniteLoading from 'v3-infinite-loading';

export default {
  name: 'App',
  data() {
    return {
      step: 0,
      image: '',
      myWriting : '',
      filter: '',
      postData: Data,
      more: 0,
    }
  },
  watch: {
    step(a) {
      if (a > 2) {
        this.step = 2;
      } else if (a < 0) {
        this.step = 0;
      }
    },
  },
  components: {
    ContainerBox,
    InfiniteLoading,
  },
  methods: {
    infiniteHandler($state) {
      axios
        .get(`https://sameron1205.github.io/vue_instagram_deploy/more${this.more}.json`)
        .then((result) => {
          setTimeout(() => {
            this.postData.push(result.data);
            this.more++;
              
            this.more<3 ? $state.loaded() : $state.complete();
          }, 500);
        });
    },
    likeFunc(val) {
      if(this.postData[val].liked == false) {
        this.postData[val].likes++;
        this.postData[val].liked = true;
      } else {
        this.postData[val].likes--;
        this.postData[val].liked = false;
      }
    },
    markFunc(val) {
      if(this.postData[val].marked == false) {
        this.postData[val].marked = true;
      } else {
        this.postData[val].marked = false;
      }
    },
    delFunc(val) {
      this.postData.splice(val,1);
    }, 
    upload(e) {
      const file = e.target.files;
      let url = URL.createObjectURL(file[0]);
      this.image = url;
      this.step++;
    }, 
    publish() {
      var myPost = {
        name: "Guest",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.image,
        likes: 0,
        date: "Î∞©Í∏à Ï†Ñ",
        liked: false,
        marked: false,
        content: this.myWriting,
        selectedFilter: this.filter,
      };
      this.postData.unshift(myPost);
      this.step = 0;
    },
  },
}
</script>

