<template>
  <div>
    <!-- Î©îÏù∏ ÌôîÎ©¥ -->
    <div v-if="step == 0">
      <PostBox :post="e" v-for="(e, idx) in postData" :key="idx" @likeEvent="changeLike(idx)" @markEvent="checkMark(idx)" @delEvent="postDel(idx)"/>
    </div>

    <!-- ÏÇ¨ÏßÑ ÏóÖÎ°úÎìú ÌôîÎ©¥ -->
    <div v-if="step == 1">
      <div :class="`${selectedFilter} upload-image`" :style="`background-image:url(${image})`"></div>
      <div class="filters">
        <FilterBox :filter="e" :image="image" v-for="e in instagramFilter" :key="e" @selected="changeFilter($event)">
          {{e}}
        </FilterBox>
      </div>
    </div>

    <!-- Í∏Ä ÏûëÏÑ± ÌôîÎ©¥ -->
    <div v-if="step == 2">
      <div :class="`${selectedFilter} upload-image`" :style="`background-image:url(${image})`"></div>
      <div class="write">
        <textarea @input="$emit('write', $event.target.value)" class="write-box">write!</textarea>
      </div>
    </div>
  </div>
</template>

<script>
import PostBox from "./PostBox.vue";
import FilterBox from "./FilterBox.vue";
import filter from "../filter.js"

export default {
  name: "ContainerBox",
  components: {
    PostBox,
    FilterBox,
  },
  props: {
    postData: Array,
    step: Number,
    image: String,
  },
  data() {
    return {
      instagramFilter: filter,
      selectedFilter: '',
    }
  },
  methods: {
    changeFilter(val) {
      this.selectedFilter = val;
      this.$emit('filterChanged', val);
    },
    changeLike(val) {
      this.$emit('likeChanged', val);
    },
    checkMark(val) {
      this.$emit('markChecked', val);
    },
    postDel(val) {
      this.$emit('postDeleted', val);
    },
  },
};
</script>

